import interfaces from '../../utils/interfaces.js';

export default {
	data() {
		return {
			tempFace: ""
		};
	},
	onLoad(params) {
		var tempFilePath = params.tempFilePath;
		this.tempFace = tempFilePath;
	},
	methods: {
		// 重新选择头像
		changePendingFace() {
			var that = this;
			uni.chooseImage({
				count: 1,
				sizeType: ["compressed"],
				sourceType: ["album"],
				success(res) {
					// 获得临时路径
					var tempFilePath = res.tempFilePaths[0];
					that.tempFace = tempFilePath;
				}
			})
		},
		upload() {
			var that = this;
			var globalUser = that.getGlobalUser("ele_login");
			
			uni.showLoading({
				mask: true,
				title: "上传中，请稍后"
			});
			
			uni.uploadFile({
				url: interfaces.uploadFace,
				data: {userId: globalUser.userId},
				filePath: that.tempFace,
				name: "file",
				success(res) {
					// debugger;
					var resDataStr = res.data;
					// console.log(typeof(resDataStr));
					var resData = JSON.parse(resDataStr);
					// console.log(typeof(resData));
					if (resData.status == 200) {
						// 获得最新的用户数据
						var userInfo = resData.data;
						uni.setStorageSync("ele_login", userInfo);
						uni.navigateBack({
							delta: 1
						})
					} else if (resData.status == 502 || resData.status == 500) {
						uni.showToast({
							title: res.data.msg,
							duration: 2000
						})
					}
				},
				fail() {
					uni.showToast({
						title: '没有接口，上传失败',
						duration: 2000,
						success() {
							uni.navigateBack({
								delta: 1
							})
						}
					})
				
				},
				complete() {
					uni.hideLoading();
				}
			});
			
		}
	}
}
